import React, { useEffect, useState } from "react";
import { Button, Container, Paper, Typography } from "@mui/material";
import { Pagination, Stack } from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { getReviews } from "../../redux/reducers/reviews";
import Review from "./Review";
import { makeStyles } from "@mui/styles";

const Reviews = ({ t, mode }) => {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();

  const { reviews } = useSelector(({ reviewsReducer }) => reviewsReducer);

  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);

  useEffect(() => {
    dispatch(getReviews());
    document.title = t("Reviews");
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    setCount(Math.ceil(reviews?.reviews?.length / 20));
  }, [reviews]);

  const changeHandler = (event, value) => {
    setPage(value);
  };

  return (
    <Paper className={classes.section}>
      <Container maxWidth="lg">
        <Box className={classes.box}>
          <Typography variant="h2">{t("Reviews")}</Typography>
          <Button
            variant="contained"
            size="large"
            color="secondary"
            onClick={() => history.push("/dashboard/reviews/leave")}
          >
            {t("leaveReview")}
          </Button>
        </Box>
        {reviews?.reviews?.map((item, idx) => (
          <Review mode={mode} item={item} key={idx} />
        ))}
        {count > 10 && (
          <Stack
            spacing={2}
            mt={"50px"}
            sx={{
              "& nav": {
                display: "flex",
                justifyContent: "center",
              },
            }}
          >
            <Pagination
              count={count}
              size="large"
              page={page}
              onChange={changeHandler}
            />
          </Stack>
        )}
      </Container>
    </Paper>
  );
};

export default Reviews;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: "80px 0 119px",
    // background: "#F7F8F9",
  },

  box: {
    display: "flex",
    justifyContent: "space-between",
    marginBottom: 37,

    [theme.breakpoints.down(992)]: {
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",

      "& button": {
        width: "100%",
        marginTop: 24,
      },
    },

    [theme.breakpoints.down(768)]: {
      "& button": {
        marginTop: 16,
      },
    },
  },
}));
